<template>
    <div class="page">
        <div class="pcenter-header">
            <div class="back" @click="$router.go(-1)"></div>
            <div class="header-title"></div>
        </div>
        <!-- pcenter-login-main-->
        <div class="pcenter-login-main">
            <div data-v-4a33ca86="" class="code-wrap" style="margin-top: 0px;"><input data-v-4a33ca86="" type="text"
                                                                                      placeholder="" v-model="cellphone"></div>
            <div data-v-4a33ca86="" class="password-wrap">
                <div data-v-4a33ca86="" class="password"><input data-v-4a33ca86="" placeholder="" v-model="password" type="password">
                </div>
                <div data-v-4a33ca86="" class="switch-wrap">
                    <div data-v-4a33ca86="" role="switch" aria-checked="false" class="van-switch">
                        <div data-v-4a33ca86="" class="van-switch__node"></div>
                    </div>
                </div>
            </div>
            <div class="sure-btn" @click="onLogin()"></div>
            <div class="fastreg-wrap">
                <div><img src="../../assets/images/user/my/11.png" alt="">
                    
                </div>
                <div @click="$router.push('/register')"><img src="../../assets/images/user/my/22.png" alt="">
                    
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import  {Toast} from  "vant"
    import Vue from "vue"
    import {mapActions} from "vuex"
    import md5 from 'js-md5';
    Vue.set(Toast)
    export default {
        data(){
            return {
                cellphone:"",
                password:""
            }
        },
        created(){
            // 
            this.isSumit =true;
        },
        mounted(){
            document.title = this.$route.meta.title
        },
        methods:{
            ...mapActions({
                getLogin:"user/getLogin",

            }),
            async onLogin(){
                if(this.cellphone.match(/^\s*$/)){
                    Toast("");
                    return;
                }
                if(!this.cellphone.match(/^1[0-9][0-9]\d{8}$/)){
                    Toast("");
                    return;
                }
                if(this.password.match(/^\s*$/)){
                    Toast("");
                    return;
                }

                    let loginData = await this.getLogin({cellphone:this.cellphone,password:this.password});
                    if (loginData.code === 200) {
                        if(this.$route.query.from==='reg'){
                            this.$router.go(-3);
                        }else{
                            this.$router.go(-1);
                        }
                    }else {
                        Toast(loginData.data);
                    }

            }
        }
    }
</script>

<style scoped>
    .page {
        width: 100%;
        height: 100vh;
        background: #FFF;
    }

    .pcenter-header {
        position: relative;
        display: flex;
        align-items: center;
        width: 100%;
        height: 1rem;
        background: #ffF;
        border-bottom: 1px solid #efefef;
    }

    .pcenter-header .back {
        width: .8rem;
        height: .8rem;
        background: url(".././../assets/images/user/my/icon1.png") no-repeat center;
        -webkit-background-size: 100%;
        background-size: 100%;
    }

    .pcenter-header .header-title {
        font-size: .32rem;
        text-align: center;
        line-height: 1rem;
        position: absolute;
        z-index: 1;
        left: 50%;
        top: 50%;
        -webkit-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
    }

    /*pcenter-login-main*/
    .pcenter-login-main {
        width: 100%;
        padding-top: 1.4rem;

    }
    .code-wrap {
        width: 90%;
        height: .84rem;
        margin: 0 auto;
        border-radius: 2px;
        border: 1px solid #efefef;

    }
    .code-wrap input {
        width: 92%;
        height: 82%;
        font-size: .28rem;
        padding-left: .2rem;
        border: 0;
        background: none;
        outline: none;
    }
.password-wrap {
    width: 90%;
    height: .84rem;
    margin: 0 auto;
    border-radius: 2px;
    border: 1px solid #efefef;
    margin-top: .4rem;
}
    .password {
        width: 80%;
        height: 100%;
    }
    .password input {
        width: 92%;
        height: 82%;
        font-size: .28rem;
        padding-left: .2rem;
        border: 0;
        background: none;
        outline: none;
    }
    .sure-btn {
        width: 85%;
        height: .8rem;
        margin: 0 auto;
        background: #eb1625;
        font-size: .36rem;
        color: #fff;
        line-height: .8rem;
        text-align: center;
        margin-top: .4rem;
        border-radius: 4px;
    }
    .fastreg-wrap {
        width: 85%;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        display: -webkit-flex;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
        justify-content: space-between;
        -webkit-justify-content: space-between;
        font-size: .24rem;
        margin: 0 auto;
        margin-top: .3rem;
    }
    .fastreg-wrap img {
        width: .32rem;
        height: .32rem;
        vertical-align: middle;
    }
    .fastreg-wrap div {
        display: flex;
        box-sizing: border-box;

    }
</style>